// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'

import axios from 'axios'

import VueRouter from 'vue-router';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import './css/font.css'

import Home from './components/Home';
import CourseJS from './components/CourseJS/CourseJS';
import CourseSZ from './components/CourseSZ/CourseSZ';
import Login from './components/Login';
//论坛
import forumhome from './webpages/forum/forumhome'
import onearticle from './webpages/forum/onearticle'
import articlelist from './webpages/forum/articlelist'
import addarticle from './webpages/forum/addarticle'
import topiclist from './webpages/forum/topiclist'
import QandA from'./webpages/forum/QandA'
import secret  from './webpages/forum/secret'
import message from './webpages/forum/message'
import editarticle from './webpages/forum/editarticle'

//课程建设
import TeaInfo from './components/CourseJS/TeaInfoLink';
import KCJS from './components/CourseJS/KCJS';
import KCZG from './components/CourseJS/KCZG';
import ts1dg from './components/CourseJS/ts1dg';
import ts2dg from './components/CourseJS/ts2dg';
import ts1rl from './components/CourseJS/ts1rl';
import ts2rl from './components/CourseJS/ts2rl';
//课程设置
import zjkydg from './components/CourseSZ/zjkydg';
import kswd from './components/CourseSZ/kswd';
import tlbl from './components/CourseSZ/tlbl';
import qjst from './components/CourseSZ/qjst';
import ttss from './components/CourseSZ/ttss';
import jxfy from './components/CourseSZ/jxfy';
import yyst from './components/CourseSZ/yyst';
import ddss from './components/CourseSZ/ddss';
import zjkyjxrl from './components/CourseSZ/zjkyjxrl';
//课程教案
import pptlist from './webpages/pptpages/list';
//教学视频
import video from './webpages/video/video';
import weike from './webpages/video/weike';
import Test from './components/Test';
import ll from './components/ll';

//教师菜单
import teaindex from './webpages/tea/teaindex'
import pptmana from './webpages/pptpages/pptmana'
import videomana from './webpages/video/videomana'

//试卷部分
import addtest from './webpages/testpages/addtest';
import addnews from './webpages/testpages/addnews';
import atest from './webpages/testpages/atest';
import anews from './webpages/testpages/anews';
import testlist from './webpages/testpages/testlist';
import level4 from './webpages/testpages/level4';
import level6 from './webpages/testpages/level6';
import news from './webpages/testpages/news';


//阅读
import readlist from './webpages/readingpages/readlist';
import readadd from './webpages/readingpages/readadd';
import oneread from './webpages/readingpages/oneread';
//图标
import {library} from '@fortawesome/fontawesome-svg-core';
import {Fontawesome, FontAwesomeIcon} from '@fortawesome/vue-fontawesome';
axios.defaults.withCredentials=true;
import{
  faPlay,
    faPause,
    faStop,
    faVolumeUp,
    faVolumeMute,
    faExpand,
} from '@fortawesome/free-solid-svg-icons';
library.add(
  faPlay,
  faPause,
  faStop,
  faVolumeUp,
  faVolumeMute,
  faExpand,
);
Vue.component('font-awesome-icon',FontAwesomeIcon);

Vue.use(VueRouter);
Vue.use(ElementUI);
const routes=[
 {path:'/',component:Home},
 {path:'/ll',component:ll},
 { path:'/login',component:Login},
  {path:'/test',component:Test},
  {path:'/coursejs',name:"coursejsLink",component:CourseJS},
    {path:'/teainfo',name:'teainfoLink',component:TeaInfo},
    {path:'/kcjsinfo',name:'kcjsLink',component:KCJS},
    {path:'/kczginfo',name:'kczgLink',component:KCZG},
    {path:'/ts1dg',name:'ts1dg',component:ts1dg},
    {path:'/ts2dg',name:'ts2dg',component:ts2dg},
    {path:'/ts1rl',name:'ts1rl',component:ts1rl},
    {path:'/ts2rl',name:'ts2rl',component:ts2rl},
  {path:'/coursesz',name:"courseszLink",component:CourseSZ},
      {path:'/zjkydg',name:"zjkydgLink",component:zjkydg},
      {path:'/kswd',component:kswd},
      {path:'/tlbl',component:tlbl},
      {path:'/qjst',component:qjst},
      {path:'/ttss',component:ttss},
      {path:'/jxfy',component:jxfy},
      {path:'/yyst',component:yyst},
      {path:'/ddss',component:ddss},
      {path:'/zjkyjxrl',component:zjkyjxrl},
      //课件
      {path:'/ppt/list',name:'pptlist',component:pptlist},
      //视频
      {path:'/video',name:'video',component:video},
      {path:'/weike',name:'weike',component:weike},
      //听力
     {path:'/tests/list',name:'testlist',component:testlist},
     {path:'/tests/level4',name:'level4list',component:level4},
     {path:'/tests/level6',name:'level6list',component:level6},
     {path:'/tests/news',name:'news',component:news},
     {path:'/tests/atest/:id',name:'atest',component:atest},
     {path:'/tests/anews/:id',name:'anews',component:anews},
     {path:'/teaindex/add',name:'addtest',component:addtest},
     {path:'/teaindex/addnews',name:'addnews',component:addnews},
     //阅读
     {path:'/read/list',name:'readlist',component:readlist},
     {path:'/read/oneread/:id',name:'oneread',component:oneread},
     {path:'/teaindex/readadd',name:'addread',component:readadd},

     //教师管理
     {path:'/teaindex',name:'teaindex',component:teaindex},
     {path:'/teaindex/pptmana',name:'pptmana',component:pptmana},
     {path:'/teaindex/videomana',name:'videomana',component:videomana},
     
   
     //论坛
     {path:'/forum',component:forumhome,
        children:[
          {path:'article/:id',component:onearticle},
          {path:'lists',name:'forum',component:articlelist},
         {path:'add',component:addarticle},
         {path:'topic',component:topiclist},
         {path:'qanda',component:QandA},
         {path:'secret',component:secret},
         {path:'message/:id',component:message},
         {path:'edit/:id',component:editarticle}
     ]},
   
   {path:'*',redirect:'/'}
]
const router = new VueRouter({
  //base:'/',
  base:'/dhu',
  routes, 
  mode:'history'
})

Vue.prototype.$http = axios

Vue.config.productionTip = false


new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



