<template>
	<div class="container">
	<div v-if="!submitted">
	<!-- 步骤条 -->
		<el-steps :active="nowstep" align-center>
		  <el-step title="步骤 1：上传生词表" description="一行一个，点击预览检查确认再下一步"></el-step>
		  <el-step title="步骤 2：上传题目" description="一行一题题面，点击预览检查确认再下一步"></el-step>
		  <el-step title="步骤 3：上传答案" description="一行一题答案，点击预览检查确认再下一步"></el-step>
		  <el-step title="步骤 4：上传原文" description="点击预览检查确认再下一步"></el-step>
		  <el-step title="步骤 5：上传音频" description=""></el-step>
		</el-steps>
		<el-button style="margin-top: 12px;"  type="primary" plain @click="laststep">上一步</el-button>
		<el-button style="margin-top: 12px;" @click="nextstep">下一步</el-button>

	<!-- 生词表处理 -->
	<el-row :gutter="40"  v-show="nowstep == 0" class="deltext" >
	<div class="subtitle">
		<strong>步骤 1：上传生词表</strong><br>
		<div class="smaller">
			一行一个，点击预览检查确认再下一步。
		</div>
		<el-divider></el-divider>
	</div>
	  <el-col :span="12">
	  	<el-input
		  type="textarea"
		  :autosize="{ minRows: 5}"
		  placeholder="请输入词汇文本"
		  v-model="words">
		</el-input>
		 <el-button v-on:click="getword">点击预览</el-button>
	  </el-col>
	  <el-col :span="12">
	  <el-card class="box-card">
		  <strong v-if="tes.words.length>0" >生词表</strong>
	  	<div class="qq" v-for="(qq,index) in tes.words" v-bind:key="index">
	  		<strong>{{index+1}}:</strong><br>
	  		&nbsp;&nbsp;{{qq}}<br>
	  	</div>
	  </el-card>
	  </el-col>
	</el-row>

	<!-- 生词表处理 -->
	<el-row :gutter="40"  v-show="nowstep == 1" class="deltext" >
	<div class="subtitle">
		<strong>步骤 2：上传题目</strong><br>
		<div class="smaller">
			一行一题题面，点击预览检查确认再下一步。
		</div>
		<el-divider></el-divider>
	</div>
	  <el-col :span="12">
	  	<el-input
		  type="textarea"
		  :autosize="{ minRows: 5}"
		  placeholder="请输入题目文本，一行一题"
		  v-model="textarea">
		</el-input>
		 <el-button v-on:click="getques">点击预览</el-button>
	  </el-col>
	  <el-col :span="12">
	  <el-card class="box-card">
		  <strong v-if="tes.ques.length>0" >题目</strong>
	  	<div class="qq" v-for="(qq,index) in tes.ques" v-bind:key="index">
	  		<strong>{{index+1}}:</strong><br>
	  		&nbsp;&nbsp;{{qq}}<br>
	  	</div>
	  </el-card>
	  </el-col>
	</el-row>

	<!-- 答案处理 -->
	<el-row :gutter="40"  v-show="nowstep == 2" class="deltext" >
	<div class="subtitle">
		<strong>步骤 3：上传答案</strong><br>
		<div class="smaller">
			一行一题答案，点击预览检查确认再下一步
		</div>
		<el-divider></el-divider>
	</div>
	  <el-col :span="12">
	  	<el-input
		  type="textarea"
		  :autosize="{ minRows: 5}"
		  placeholder="请输入答案文本"
		  v-model="textarea2">
		</el-input>
		 <el-button v-on:click="getans">点击预览</el-button>
	  </el-col>
	  <el-col :span="12">
	  <el-card class="box-card">
	  	<div class="qq" v-for="(aa,index) in tes.ans" v-bind:key="index">
	  		<strong>Q{{index+1}}</strong>
	  		&nbsp;{{aa}}<br>
	  	</div>
	  </el-card>
	  </el-col>
	</el-row>

	<!-- 听力原文 -->
	<el-row :gutter="40"  v-show="nowstep == 3" class="deltext" >
	<div class="subtitle">
		<strong>步骤 4：上传原文</strong><br>
		<div class="smaller">
			可用html语法加强文本显示<br>
			空格使用&amp;nbsp;<br> 
			换行调整可以直接使用<br>
			例如: 使用strong标签加粗<br>&lt;strong&gt; title&lt;/strong&gt;=><strong>title</strong><br/>
		</div>
		<el-divider></el-divider>
	</div>
	  <el-col :span="12">
	  	<el-input
		  type="textarea"
		  :autosize="{ minRows: 5}"
		  placeholder="请输入听力原文"
		  v-model="textarea3">
		</el-input>
		 <el-button v-on:click="getorg">点击预览</el-button>
	  </el-col>
	  <el-col :span="12">
	  <el-card class="box-card">
	  <div class="qq">
	  	<div class="ok" v-html="tes.org"></div>
	  </div>
	
	  </el-card>
	  </el-col>
	</el-row>

	<!-- 上传mp3 -->
	<el-row :gutter="40"  v-show="nowstep == 4" class="deltext" >
	<div class="subtitle">
		<strong>步骤 4：上传mp3</strong><br>
		<div class="smaller">
		</div>
		<el-divider></el-divider>
		<!-- 上传part -->
		<el-upload
			  class="upload-file"
			  drag
			  action="http://47.96.87.249:8080/dhuf/docu_upload"
			  :on-success="uploadSuccess"
			 >
			  <i class="el-icon-upload"></i>
			  <div class="el-upload__text">
			  	将文件拖到此处，或<em>点击上传</em>
			  </div>
		</el-upload>
	</div>
	</el-row>

	<!-- 上传数据确认 -->
	<el-row :gutter="40"  v-show="nowstep == 5" class="deltext" >
	<div class="subtitle" style="font-size:16px;font-weight:300">
		<el-row :gutter="20">
			<el-col :span="8">
			试卷标题：</el-col>
			<el-col :span="12">
			<el-input v-model="tes.title"/>
		</el-col>
		</el-row>
		<el-row :gutter="20" class="deltext">
			<el-col :span="8">
			作者：</el-col>
			<el-col :span="12">
			<el-input v-model="tes.author" />
			</el-col>
		</el-row>
		<div  class="deltext">
		确认上传请点击下一步。</div>
	</div>
	</el-row>
	</div>

	<div v-if="submitted" class="successtip">
		<h2>您的数据已上传 successful!</h2>
		      <img src="../../assets/success.jpg" class="roundimg">
		      <div style="padding: 14px;">
		        <div class="bottom clearfix">
		          <time class="time">{{ currentDate }}</time>
		        </div>
		      </div>
	</div>
	</div>
</template>
<script>
	import axios from 'axios'
	export default{
		data(){
			return{
				tes:{
					title:'',
					author:'',
					ques:[],
					ans:[],
					org:'',
					lis:'',
					words:[]
				},
				currentDate: new Date(),
				words:'',
				textarea:'',
				textarea2:'',
				textarea3:'',
				nowstep:0,
				submitted:false
			}
		},
		methods:{
			getword:function(){
				this.tes.words=[];
				var tmp=this.words;
				var pos=tmp.indexOf("\n");
				var nowq;
				while(pos!=-1&&tmp!=''){
					var ww=tmp.slice(0,pos);
					tmp=tmp.slice(pos+1);
					pos=tmp.indexOf("\n");
					if(ww.length>0)
						this.tes.words.push(ww);
				}
				if(tmp!='')
					this.tes.words.push(tmp);
			},
			getques:function(){
				this.tes.ques=[];
				var tmp=this.textarea;
				var pos=tmp.indexOf("\n");
				while(pos!=-1&&tmp!=''){
					var ww=tmp.slice(0,pos);
					tmp=tmp.slice(pos+1);
					pos=tmp.indexOf("\n");
					if(ww.length>0)
						this.tes.ques.push(ww);
				}
				if(tmp!='')
					this.tes.ques.push(tmp);
			},
			getans:function(){
				this.tes.ans=[];
				var tmp=this.textarea2;
				var pos=tmp.indexOf("\n");
				while(pos!=-1&&tmp!=''){
					var ww=tmp.slice(0,pos);
					tmp=tmp.slice(pos+1);
					pos=tmp.indexOf("\n");
					if(ww.length>0)
						this.tes.ans.push(ww);
				}
				if(tmp!='')
					this.tes.ans.push(tmp);
			},
			getorg:function(){
				this.tes.org=this.textarea3.replace(/\n/g,"<br>");
				// this.tes.org=this.tes.org.replace(/\s/g,"&nbsp;");不能够换。
				console.log(this.tes.org);
			},
			//上传的钩子函数返回url
			uploadSuccess(response,file,fileList){
				alert("mp3 upload success")



var msg = response;
      var obj = JSON.parse(msg[0]);
      console.log(obj.status);
      if (obj.status == "success") {
        this.$message({
          message: "上传成功",
          type: "success"
        });
      } else if (obj.status == "exist") {
        this.$message({
          message: "已经有这个文件了",
          type: "warning"
        });
      } else if (obj.status == "nofile") {
        this.$message({
          message: "后台没有接收到消息",
          type: "warning"
        });
      } else {
        this.$message.error("未知的错误");
      }
				this.tes.lis=file.name;
				// console.log(this.tes);
			},
			post:function(){
				console.log(this.tes);
				axios.post("http://47.100.60.171:5000/api/listes/addnews",this.tes)
				.then((data)=>{
					//console.log(data);
					this.submitted=true;
					var msg=data.data;
					var status=JSON.parse(msg[0]).status;
					if(status=="Success"){
						this.$message({
							message:"上传成功",
							type:"success"
						})
					}else {
						this.$message({
							message:"其他错误",
							type:"error"
						})
					}
				});
			},
			// 下一步
			nextstep(){
				if(this.nowstep<5)
					this.nowstep+=1;
				else
					this.post();
			},
			//上一步
			laststep(){
				if(this.nowstep>0)
				this.nowstep-=1;
			}
		}
	}
</script>
<style scoped>
	.container{
		margin-top: 90px;
		padding: 1em 6em 4em 6em;
	}
	.box-card .qq{
		text-align: left;
		max-width: 100%
	}
	.ok{
		width: 100%;
		margin: 1rem;
	}
	.deltext{
		padding-top: 2em
	}
	.el-button{
		margin-top: 1em;
	}
	.subtitle{
		text-align: center;
		font-size: 20px;
	}
	.smaller{
		margin-top: 0.5em;
		font-size: 16px;
	}
	.roundimg{
		border-radius: 50%;
		max-width: 200px;
		border: 10px solid #FFCC99;
	}
</style>


