<template>
	<div class="container">
		<div class="dati">
		<h2 class="textcenter">--新闻听力：{{tes.title}}--</h2>
		
		<h4 class="textcenter" style="color:grey">分类：
			<el-tag v-if="tes.tag === 'level4' " type="success">cet4</el-tag>
			    <el-tag v-else-if="tes.tag === 'level6' " type="danger">cet6</el-tag>
			    <el-tag v-else-if="tes.tag === 'news' " type="warning">news</el-tag>
		<el-tag v-else type="info">oth</el-tag>
		</h4>
		
		<div v-show="loading">
			<el-button icon="el-icon-loading" 
		            	circle></el-button>
			<h4>loading...waiting...</h4>
		</div>
		<div class="test" v-show="!loading">
			<div class="intest">
				<div v-show="tes.words.length>0" style="text-align:center">
			生词tips:
			<div v-for="(qq,ind) in tes.words" v-bind:key="ind">
			{{ind+1}}.{{qq}}<br>
			</div>
		</div>
			<div v-for="(qq,ind) in tes.ques" v-bind:key="ind">
				<span class="tihao">
					<br>
					&nbsp;&nbsp;{{ind+1}}.{{qq}}
					<br><br>
					<el-input
						type="textarea"
						autosize
						placeholder="请输入内容"
						:disabled="submitted"
						v-model="userans[ind]">
					</el-input>
					<span v-if="submitted"><br><br>
						答案：{{tes.ans[ind]}}
					</span>
				</span>
				<el-divider></el-divider>
			</div>
			<el-row :gutter="20">
			 <el-col :offset="12">
			 	<el-button v-if="!submitted" type="primary" @click="getmark" round>提交</el-button>
			 	<el-button v-else type="danger" @click="seeans=true" round>查看原文</el-button>
			 </el-col>
			</el-row>
		</div>
		</div>
		</div>

		<div class="daan" v-if="seeans">
			<h2 style="text-align:center;color:#FF4040">--听力原文及答案解析--</h2>
			<div class="test" style="border-color:#F08080;background-color:#FFF5EE">
				<div class="intest inans">
				<div v-html="tes.org"></div>
			</div>
			</div>
		</div>

<!-- 播放器 -->
 <div class="player">
    <el-row>
    <!-- 音量控制 -->
      <el-col :span="4">
        <!-- 弹窗 -->
        <el-popover placement="top-start" title="音量控制" trigger="hover">
	          <div style="text-align: center">
	          		<!-- 音量进度条 -->
		            <el-progress type="circle" 
			 	:percentage="music.volume"></el-progress>
		            <br>
		            <!-- 音量增加减少按钮 -->
		            <el-button icon="el-icon-remove-outline" 
		           	 @click="changeVolume(-10)"
		            	circle></el-button>
		            <el-button icon="el-icon-circle-plus-outline"
		            	@click="changeVolume(10)"
		           	 circle></el-button>
	          </div>
	          <!-- 触发音量按钮 -->
	          <el-button  id="play" slot="reference"
	          :icon="music.isPlay?'el-icon-close':'el-icon-caret-right'" 
	          @click="play"
	          circle></el-button>
        </el-popover>
      </el-col>
      <!-- 播放滑块 -->
      <el-col :span="14"  style="padding-left: 0px">
        <el-slider
        	@change="changeTime"
        	:format-tooltip="formatTime"
        	:max="music.maxTime"
        	v-model="music.currentTime" 
        style="width: 100%;"></el-slider>
      </el-col>
      <el-col :span="3" style="padding: 9px 0px 0px 10px;color:#909399;font-size: 13px">
        {{formatTime(music.currentTime)}}/{{formatTime(music.maxTime)}}
      </el-col>
    </el-row>
    <audio ref="music" name="musiclabel" id="musicid" @canplay="getDuration" :src="musicroute"> 
      <!-- <source v-bind:src="musicroute" type="audio/mpeg"> -->
    </audio>


  </div>
	</div>
</template>

<script>
import axios from 'axios'
	export default{

		name:"atest",
		data(){
			return{
				id:this.$route.params.id,
				tes:{},
				userans:[],
				loading:true,
				res:[],
				submitted:false,
				seeans:false,
				rcnt:0,
				tot:0,

				//播放器
				changetag:false,
				 musicroute:'',
				        music:{
				          isPlay:false,
				          currentTime:0,
				          maxTime:0,
				          volume:100
				        }
			}
		},
		
		created(){
	axios.get("http://47.100.60.171:5000/api/listes/list/"+this.id)
			.then(function(data){
				return data.data;
			})
			.then((data)=>{
				console.log(data);
				if(data.status=="Success"){
					this.$message({
						offset:200,
						message:"加载成功",
						type:"success"
					});
					this.tes=data.test;
					this.loading=false;
					if(this.tes.lis!=""&&this.tes.lis!=null){
						this.musicroute="http://47.96.87.249:8080/dhuf/docu_download?docu_name="+this.tes.lis;
					}
				}
				else{
					this.$message({
						message:"后台错误",
						type:"error"
					})
				}
			})
		},
		mounted(){
		      this.$nextTick(()=>{
		      	this.changetag = !this.changetag
		        setInterval(this.listenMusic,1000)
		      })
		},
		watch:{
			changetag(){
				this.musiclabel.load()
				//alert(this.$refs.music.duration)
			}
		},
		methods:{
			check(){
      
    },
			getmark:function(){
				var tp=this;
          tp.$message({
            offset:200,
            message:"亲爱的同学，您已成功提交",
            type:"success"
	});
	tp.submitted=true;		
			},
getDuration(){
	//alert('her ')
	//alert(this.$refs.music.duration)
},
// 播放器part
listenMusic(){
        if(!this.$refs.music){
          return
        }
        if(this.$refs.music.readyState){
          this.music.maxTime = this.$refs.music.duration
        }
        this.music.isPlay=!this.$refs.music.paused
        this.music.currentTime = this.$refs.music.currentTime
      },
      play(){
        if(this.$refs.music.paused){
          this.$refs.music.play()
        }else{
          this.$refs.music.pause()
        }
        this.music.isPlay=!this.$refs.music.paused
        this.$nextTick(()=>{
          document.getElementById('play').blur()
        })
      },
      changeTime(time){
        this.$refs.music.currentTime = time
      },
      // 音量改变
      changeVolume(v){
        this.music.volume += v 
        if(this.music.volume>100){
          this.music.volume = 100
        }
        if(this.music.volume<0){
          this.music.volume = 0
        }
        this.$refs.music.volume = this.music.volume/100
      },
      formatTime(time){
        let it = parseInt(time)
        let m = parseInt(it/60)
        let s = parseInt(it%60)
        return (m<10?"0":"")+parseInt(it/60)+":"+(s<10?"0":"")+parseInt(it%60)
      }
    }  
}
</script>

<style scoped>
	.container{
		margin-top: 100px;
	}
	.textcenter{
		text-align: center;
	}
	.test{
		margin-top: 3em;
		margin-bottom: 10em;
		margin-right: 10em;
		margin-left: 10em;
		text-align: left;
		border-radius: 10px;
		border-width: 3px;
		border-style: solid;
		border-color: #CCCCFF;
		/*background-color:#F0F8FF;*/

	}
	.testres{
		font-size: 16px;
		font-weight: 900;
		color: #F56C6C;
	}
	.cho{
		margin-top: 2em;
	}
	.tihao{
		font-size: 15px;
		margin-bottom: 1em;

	}
	.intest{
		margin: 3em;
	}
	.player{
		width: 500px;
		position: fixed;
		top:80px;
   		 /*text-align: left;*/
   		 border-radius: 20px;
    		 box-shadow: 2px 2px 2px rgba(0,0,0, 0.6);
    		 background-color: #CCCCFF;
	}
</style>


