  This unit explores the topic of advertising and advertising techniques use to sell their products. Students analyze ads and share their knowledge and opinions of advertising, and use the information from the unit from the unit to create, perform, and evaluate ads.  "),_c('br'),_vm._v(" "),_c('div',{staticClass:"center-align"},[_vm._v("Unit 2 Identity Theft ")]),_vm._v("\n    This unit focuses on the topic of identity theft. If explores various ways thieves steal personal information and what steps people can take to detect and prevent identity theft. Students express their opinions and prepare a role play about identity theft prevention.  "),_c('br'),_vm._v(" "),_c('div',{staticClass:"center-align"},[_vm._v("Unit 3 Endurance Test ")]),_vm._v("\nThis unit explores the topic of extreme sports and personal characteristics endurance athletes possess. Students discuss what motivates endurance athletes to participate in extreme sports and use the information from the unit to analyze and create aphorism about motivation.  "),_c('br'),_vm._v(" "),_c('div',{staticClass:"center-align"},[_vm._v("Unit 4 Culture and Commerce ")]),_vm._v("\nThis unit focuses on the positive aspects of tourism. Students explore the topic of the effects of tourism on culture and economy, and discuss advantages and disadvantages of tourism. They also collaborate to reach a compromise on a controversial topic related to tourism.  "),_c('br'),_vm._v(" "),_c('div',{staticClass:"center-align"},[_vm._v("Unit 5 Voluntary Simplicity ")]),_vm._v("\n      This unit explores people’s decision to lead a simple life without the conveniences of the modern world. Students learn about a family of urban homesteaders who chose to live simply in the inner-city neighborhood. Students evaluate and discuss ways to simplify one’s life.  "),_c('br'),_vm._v(" "),_c('div',{staticClass:"center-align"},[_vm._v("Unit 6 Before You Say “I Do” ")]),_vm._v("\n     This unit explores the topic of marriage and the written and unwr